body {

  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
}

.main__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 35px;

  font-family: 'Exo 2', sans-serif;

  color: $color-main-font;
}

.secondary__title {

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;

  font-family: 'Exo 2', sans-serif;

  color: $color-main-font;
}

.text {
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;

  font-family: 'PT Sans', sans-serif;

  color: $color-additional-font;
}

@media screen and (min-width:$tablet) {
  .main__title {
    font-size: 44px;
    line-height: 55px;
  }

  .secondary__title {
    font-size: 20px;
    line-height: 27px;
  }

  .text {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (min-width:$desktop) {
  .main__title {
    font-size: 52px;
    line-height: 65px;
  }
}